.reading_area {
    background: $soft-black;  // $reading-background;
    margin-top: 1px;
    padding: 75px 0;
    letter-spacing: 1px;
    line-height: 1.6;
    font-size: 1.1em;
    pre {
        letter-spacing: 0;
        background: #101010;
        margin: 75px 0;
    }
    h1 {
        margin: 0 0 50px 0;
    }
    hr {
        background: $second-color;
        border: 1px solid darken($second-color, 25%);
        margin-bottom: 75px;
    }
    img {
        margin: 75px auto;
        display: block;
    }
}

.post_date,
.post_tags {
    text-align: right;
    font-size: .9em;
}

.post_tags {
    // font-family: $code-font;
    color: $second-color;
}

.share_phrase {
    padding: $margin-gaps;
    text-align: center;
    font-size: 1.2em;
    color: $second-color;
}

.share_button {
    padding: 7px;
    border: 3px solid $grey-color;
    border-radius: 50%;
    background: $soft-black;
    margin: 0 7px;
}

.a2a_kit {
    padding-bottom: 35px;
    display: flex;
    justify-content: center;
    border-radius: 50%;
}

.a2a_button_twitter,
.a2a_button_whatsapp,
.a2a_button_google_gmail,
.a2a_button_linkedin,
.a2a_button_reddit,
.a2a_button_facebook {
    border-radius: 50%;
}

// on blog main

.post_rep {
    display: block;
    margin: $margin-gaps-double;
    p {
        margin-bottom: 0;
    }
    .post_title {
        font-family: $main-font;
        font-size: 1.2em;
        transition: none;
    }
    .read {
        visibility: hidden;
        transition: .3s;
        font-size: 0.8em;
        color: $grey-color;
        @media only screen and (max-width: 925px) {
          display: none;
        }
    }
    .excerpt {
        display: flex;
        justify-content: flex-start;
        align-items: baseline;
        border-left: 3px solid darken($brand-color, 35%);
        transition: .3s;
        margin: 5px 10px 5px 0;
    }
    blockquote {
        background: inherit;
        border: none;
        font-size: .95em;
        color: lighten($grey-color, 35%);
        margin: 5px 2vh 5px 0;
        padding: 0 1vh;
    }
    .post_date {
        color: lighten($grey-color, 20%);
        font-size: 0.9em;
    }
}

.post_rep:last-child {
    margin-bottom: 0;
}

.post_rep:hover {
    .read {
        visibility: visible;
        color: #ff7733;
    }
    .excerpt {
        border-left: 10px solid darken($brand-color, 35%);
        margin-right: 0;
    }
}
br.phone_breakline {
    display: none;
    @media only screen and (max-width: 800px) {
        display: block;
    }
}

#blogSoftwareButton {
    background: #1d6240;
}

#EMPosts {
    display: none;
}

// search
.search-input {
    background: $soft-black;
    margin: 2rem auto;
    display: block;
    width: 80%;
    padding: 5px;
    font-family: $main-font;
    font-size: 1em;
    color: #fff;
    border: .2rem solid #000;
    border-radius: 7px;
}

.found-area {
    p { 
        margin: 0; 
    }
    a {
        text-decoration: none;
    }
    .excerpt {
        color: lighten($grey-color, 40%);
        font-size: .9em;
    }
    .date {
        color: lighten($grey-color, 20%);
        font-size: .9em;
    }
    .tag {
        font-size: .9em;
        color: $second-color;
    }
}
.found-card {
    padding: 1rem;
    background: darken($reading-background, 1%);
    transition: .3s ease;
}
.found-card:hover {
    background: lighten($reading-background, 2%);
}
.search-not-found {
    text-align: center;
    display: block;
}

.black-line {
    border-color: #000;
    background-color: #000;
}

.excerpt-in-post {
    font-size: 1.2em;
    color: lighten($grey-color, 15%);
    text-align: center;
}

// 6.8.21' picked up posts style

.picked-up-posts-wrapper {
    padding-bottom: 7rem;
}

.picked-up-posts {
    display: flex;
    justify-content: space-around;
    padding: 0;
} @media only screen and (max-width: 925px) {
    .picked-up-posts {
        display: block;
        justify-content: inherit;
        margin: 0;
        max-width: 100%;
    }
}

div.picked-up-post-box {
    background-image: var(--background);
    background-position: center;
    background-size: cover;
    
    min-height: 15rem;
    margin: 3rem;
    padding: 1rem;
    position: relative;
    border-radius: .5rem;
} @media only screen and (max-width: 925px) {
    div.picked-up-post-box {
        margin: 7rem 0;
        padding: 0;
        border-radius: 0;
    }
}
.picked-up-post-box:hover {
    > .picked-up-def {
        opacity: 1;
    }
}

a.picked-up-post-box, a.picked-up-post-box:hover {
    width: 100%;
    transition: none;
    text-decoration: none;
    background: none;
}

.picked-up-def {
    padding: .5rem;
    background: #000;
    opacity: .6;
    bottom: -2rem;
    left: 1rem;
    right: 1rem;
    position: absolute;
    text-align: center;
    transition: .5s ease;
} @media only screen and (max-width: 925px) {
    .picked-up-def {
        opacity: .85;
        border-radius: .5rem;
    }
}

.picked-up-post-title,
.picked-up-post-date {
    margin: 0;
    padding: .1rem;
    font-weight: bold;
}

.picked-up-post-title {
    background-color: #124;
}
.picked-up-post-date {
    text-align: center;
}
